body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Main {
  position: relative;
  background-color: rgb(255, 255, 255);
  width:100%;
  height:100vh;
}
.landing-container {
    position:relative;
    flex-direction: column;
    align-content: flex-end;
    height:100vh;
}

.landing-text {
    text-align: center;
    position: absolute;
    top: 30%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100%;
}

h3 {
    font-weight: 200;
    letter-spacing: 4px;
    font-size:32px
  }

.parallax-container {
    width:100%;
    bottom:0;
    overflow: hidden;
    position: absolute;
    height: 100%;
}

.bgimg {
    background-image: url(/static/media/vancouver_skyline_vec.70da0182.svg);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-position: bottom;
    background-size: cover;
    height:100vh;
}
.footer-container {
    height: 20vh;
    flex-direction: column;
    align-content: flex-end;
    position: relative;
}

.parallax-container-footer {
    width:100%;
    overflow: hidden;
    height: 30vh;
    position: absolute;
}

.bgimg1 {
    background-image: url(/static/media/mountains.d4081144.svg);
    /* transform: scale(1.1); */
    background-position: top;
    background-size: cover;
    -webkit-transform: translate(5%, 0);
            transform: translate(5%, 0);
    z-index: 10;
    height:40vh;
    width:140%
}

.bgimg2 {
    background-image: url(/static/media/mountains.d4081144.svg);
    /* transform: scale(1.1); */
    background-position: top;
    background-size: cover;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    -webkit-transform: translate(-20%, 0);
            transform: translate(-20%, 0);
    z-index: 9;
    height:30vh;
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
    width:120%
}
.button-container{
    padding: 0px 0px 0px;
    width: auto;
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.theme-button{
    letter-spacing: .3em;
    padding: 5px 10px 5px;
    transition: all .4s ease-out;
    background: white;
    border-radius: 25px;
    text-decoration: none;
    border-style: solid;
    border: 1px solid;
}
  
.theme-button:visited {
    color:black;
}
.theme-button:hover {   
    color: white;
    border-color: black;
    background: black;
}
  

a {
    color: black;
    margin: 20px 20px;
    transition: all .4s ease-out;
    text-decoration: none;
}

a:visited { text-decoration: none; color: black; }

